.page {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0%;
  display: flex;
  flex-direction: column;
  min-height: 360px;
}

.parallax {
  flex: 1 0 100%;
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 60%);
  }

  &__list {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0%;
  }

  &__list li {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  &__bg {
    position: absolute;
    width: 110%;
    height: 110%;
    top: -5%;
    left: -5%;
    background: url('../img/background.jpg') center / cover no-repeat;
  }

  &__rope {
    position: absolute;
    width: 120%;
    left: -10%;
    opacity: 0;
    transition: all 1.0s ease 1.8s;

    & img {
      width: 100%;
    }

    &_1 {
      top: 5%;
      transform: scale(1);
    }

    &_2 {
      top: -1.6666666%;
      transform: scale(1.0555556);
    }

    &_3 {
      top: -11.6666666%;
      transform: scale(1.13889);
    }
  }

  &__elememt {
    position: absolute;
  }

  &__elememt span {
    display: inline-block;
    transform-origin: 50% 0;
    width: 280px;
    height: 280px;
