.content {}

@keyframes swing {
  0% {
    transform: rotate(10deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

@keyframes wave {
  0% {
    transform: rotate(0deg) translate3d(0, 10%, 0) rotateZ(0deg);
  }

  100% {
    transform: rotate(360deg) translate3d(0, 10%, 0) rotateZ(-360deg);
  }
}

@keyframes lighthouse {
  0% {
    transform: translate3d(15%, 0, 0) rotateZ(10deg);
  }

  100% {
    transform: translate3d(-1.6666666%, 0, 0) rotateZ(-10deg);
  }
}